<template>
  <div class="search-container">
    <!--  -->
    <form class="search-form" action="/">
      <van-search
        v-model="searchText"
        background="#3296fa"
        placeholder=""
        show-action
        @search="onSearch"
        @cancel="$router.back()"
        @focus="isResultShow = false"
      />
    </form>
    <!-- / -->

    <!--  -->
    <search-result v-if="isResultShow" :q="searchText" />
    <!-- / -->

    <!--  -->
    <search-suggestion
      v-else-if="searchText"
      :q="searchText"
      @search="onSearch"
    />
    <!-- / -->

    <!--  -->
    <search-history
      v-else
      v-model="searchHistories"
      @search="onSearch"
    />
    <!-- / -->
  </div>
</template>

<script>
import SearchResult from './components/search-result'
import SearchSuggestion from './components/search-suggestion'
import SearchHistory from './components/search-history'
import { getItem, setItem } from '@/utils/storage'

export default {
  name: 'SearchPage',
  components: {
    SearchResult,
    SearchSuggestion,
    SearchHistory
  },
  props: {},
  data () {
    return {
      searchText: '', // 
      isResultShow: false, // 
      suggestions: [], // 
      searchHistories: getItem('search-histories') || [] // 
    }
  },
  computed: {},
  watch: {
    searchHistories (newVal) {
      setItem('search-histories', newVal)
    }
  },
  created () {},
  mounted () {},
  methods: {
    onSearch (q) {
      // q: 
      // 1. 
      this.searchText = q

      // 2. 
      const searchHistories = this.searchHistories
      const index = searchHistories.indexOf(this.searchText)
      if (index !== -1) {
        searchHistories.splice(index, 1)
      }
      searchHistories.unshift(this.searchText)

      // 3. 
      this.isResultShow = true
    }
  }
}
</script>

<style scoped lang="less">
.search-container {
  padding-top: 54px;
  // 
  .search-form {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
  }
  .van-search__action {
    color: #fff;
  }
}
</style>
